        background-repeat: no-repeat;
    }

    .accordion-container {
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        margin: 10px;
        padding: 10px;
        box-sizing: border-box;
        margin-left: 0px;
        margin-right: 10px;
    }

    .accordion-heading {
        cursor: pointer;
        user-select: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .accordion-heading .toggle-icon::before {
        content: '+';
        font-size: 20px;
        margin-right: 10px;
        font-weight: bolder;
    }

    .accordion-heading.active .toggle-icon::before {
        content: '-';
    }

    .accordion-content {
        display: none;
        margin-top: 10px;
    }

